<template>
  <div class="home">
    <van-nav-bar>
      <template #left>
        <div class="title">
          {{timeTxt}}
          <span v-if="isLogin">，{{userData.nickName}}</span>
        </div>
      </template>
      <template #right>
        <van-icon class="search" name="search" size="22" @click="toSearch"/>
      </template>
    </van-nav-bar>
    <!-- 轮播图 -->
    <div class="home-box">
      <van-swipe class="my-swipe" :autoplay="5000" duration="1000" indicator-color="#fff">
        <van-swipe-item v-for="(item,index) in bannerData" :key="index" @click="viewProductDetail(item.pid)">
          <img class="auto-img" :src="item.bannerImg" />
        </van-swipe-item>
      </van-swipe>

      <div class="service-box">
        <van-grid :border="false" :column-num="2">

          <van-grid-item>
            <div class="service-item left-part">
              <div class="service-name">外送</div>
              <div class="service-icon">
                <van-icon name="logistics" size="50"/>
              </div>
            </div>
          </van-grid-item>
          <van-grid-item>
            <div class="service-item right-part">
              <div class="service-name">自提</div>
              <div class="service-icon">
                <van-icon name="bag" size="50"/>
              </div>
            </div>
          </van-grid-item>

        </van-grid>
      </div>

      <div class="hot-product-box">
        <div class="hot-product-title">为你推荐</div>
        <!-- 产品格子 -->
        <van-grid :border="false" :column-num="2">
          <van-grid-item v-for="(item,index) in hotProductData" :key="index">
            <div class="product-box" @click="viewProductDetail(item.pid)">
              <div class="product-img-box">
                <img class="auto-img" :src="item.smallImg" />
              </div>
              <div class="product-info-box">
                <div class="product-name">{{item.name}}</div>
                <div class="product-enname">{{item.enname}}</div>
                <div class="product-price">￥{{item.price}}</div>
              </div>
            </div>
          </van-grid-item>


        </van-grid>
      </div>

    </div>
  </div>
</template>

<script>
import "../../assets/less/home.less";
export default {
  name:"Home",
  data() {
    return {
      timeTxt: "",
      bannerData: [],
      hotProductData: [],
      userData: [],
      isLogin: false
    };
  },
  created() {
    this.setTimeTxt();
    this.getBannerData();
    this.getHotProductData();
    this.getuserData()
  },
  methods: {
    // 设置欢迎语
    setTimeTxt() {
      let currentHour = new Date().getHours();
      // 早6-12 下12-18 晚18-24 深24-6
      if (currentHour >= 6 && currentHour < 12) {
        this.timeTxt = "早上好";
      } else if (currentHour >= 12 && currentHour < 18) {
        this.timeTxt = "下午好";
      } else if (currentHour >= 18 && currentHour < 24) {
        this.timeTxt = "晚上好";
      } else {
        this.timeTxt = "晚安";
      }
    },

    // 轮播图
    getBannerData() {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "GET",
        url: "/banner",
        params: {
          appkey: this.appkey,
        },
      })
        .then((result) => {
          if (result.data.code == 300) {
            this.$toast.clear();
            this.bannerData = result.data.result;
          }
          // // console.log("result ==>", result);
        })
        .catch((err) => {
          this.$toast.clear();
          // // console.log("err ==>", err);
        });
    },

    // 获取热门产品数据
    getHotProductData() {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "GET",
        url: "/typeProducts",
        params: {
          appkey: this.appkey,
          key: 'isHot',
          value: 1
        },
      })
        .then((result) => {
          if (result.data.code == 500) {
            this.$toast.clear();
            this.hotProductData = result.data.result;
          }
          // // console.log("result ==>", result);
        })
        .catch((err) => {
          this.$toast.clear();
          // // console.log("err ==>", err);
        });
    },
    viewProductDetail(pid){
      this.$router.push({
        name:'Detail',
        query:{
          pid
        }
      });
    },
    getuserData(){
      let tk = localStorage.getItem('CSTK');
      // 不存在token
      if(!tk){
        return;
      }
      
      this.$toast.loading({
        message: '加载中...',
        forbidClick: true,
        duration: 0,
        loadingType: 'spinner'
      })

      this.axios({
        method: "GET",
        url: "/findMy",
        params: {
          appkey: this.appkey,
          tokenString: tk
        }
      }).then( result => {
        this.$toast.clear();
        if(result.data.code == "A001"){
          this.userData = result.data.result[0];
          this.isLogin = true;
        }
        // console.log("findMy result ==> ",result)
      }).catch( err => {
        this.$toast.clear();
        // console.log("findMy err ==> ",err)
      })
    },
    // 跳转到搜索页
    toSearch(){
      this.$router.push({name: 'Search'})
    }
  },
};
</script>

<style lang="less" scoped>
</style>